@import url('https://fonts.googleapis.com/css2?family=Cedarville+Cursive&family=Sofia+Sans:wght@1&display=swap');

* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

body {
    font-family: 'Cedarville Cursive', cursive;
    justify-content: center;
    align-items: center;
    display: flex;
    min-height: 100vh;
    background-color: #222327;
}


.menu {
    position: relative;
    width: 300px;
    height: 300px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.menu .toggle {
    background-color: #fff;
    color: #222327;
    position: absolute;
    width: 75px;
    height: 75px;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 3em;
    cursor: pointer;
    transition: 0.5s;
}

.menu .toggle.active {
    transform: rotate(315deg);
    box-shadow: 0 0 0 68px #fff;
    background: #222327;
    color: #fff;
} 
.menu li {
    position: absolute;
    left: 24px;
    list-style: none;
    transform: rotate(calc(360deg / 8 * var(--i))) translateX(40px);
    transform-origin: 140px;
    visibility: hidden;
    opacity: 0;
    transition: 0.5s;
    z-index: 10;
}
.menu.active li {
    visibility: visible;
    opacity: 1;
}

.menu li a {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 55px;
    height: 55px;
    color: #222327;
    font-size: 1.75em;
    transform: rotate(calc(360deg / -8 * var(--i)));
    border-radius: 50%;
}
.menu.active li.active {
    transform: rotate(calc(360deg / 8 * var(--i))) translateX(12px);
}

.indicator {
    position: absolute;
    left: calc(50% + 2.5px);
    transform-origin: right;
    width: 100px;
    height: 1px;
    background: transparent;
    pointer-events: none;
    transition: 0.5s;
}

.indicator::before {
    content: '';
    position: absolute;
    top: -27.5px;
    left: 72px;
    width: 55px;
    height: 55px;
    background: #222327;
    box-shadow: 0 0 0 6px #29fd53;
    border-radius: 50%;
    transition: 0.5s;
}
.menu.active .indicator::before {
    opacity: 1;
    top: -27.5px;
    left: -12.5px;
    background: #29fd53;
    box-shadow: 0 0 0 6px #222237;
}

.menu li:nth-child(2).active ~ .indicator {
    transform: translateX(-86px) rotate(0deg);
}

.menu li:nth-child(3).active ~ .indicator {
    transform: translateX(-86px) rotate(45deg);
}
.menu li:nth-child(4).active ~ .indicator {
    transform: translateX(-86px) rotate(90deg);
}

.menu li:nth-child(5).active ~ .indicator {
    transform: translateX(-86px) rotate(135deg);
}

.menu li:nth-child(6).active ~ .indicator {
    transform: translateX(-86px) rotate(180deg);
}

.menu li:nth-child(7).active ~ .indicator {
    transform: translateX(-86px) rotate(225deg);
}
.menu li:nth-child(8).active ~ .indicator {
    transform: translateX(-86px) rotate(270deg);
}

.menu li:nth-child(9).active ~ .indicator {
    transform: translateX(-86px) rotate(315deg);
}